.hero-section {
  margin-left: auto;
  margin-right: auto;
}

.container-header {
  margin-left: auto;
  margin-right: auto;

  @media screen and (max-width: 767px) {
    max-width: 480px;
    height: 580px;
    position: relative;
    padding: 40px 31px;
  }

  @media screen and (min-width: 768px) {
    max-width: 768px;
    display: flex;
    padding-left: 39px;
    padding-right: 39px;
  }

  @media screen and (min-width: 1280px) {
    max-width: 1280px;
    padding: 0 55px;
  }
}

.no-padding {
  @media screen and (min-width: 768px) {
    padding-right: 0;
  }
}

.header-sidebar {
  @media screen and (max-width: 767px) {
    width: 69px;
    position: absolute;
  }

  @media screen and (min-width: 768px) {
    display: flex;
    width: 90px;
    padding-bottom: 40px;
    align-items: space-between;

    height: 680px;
    margin-right: 30px;
    z-index: 99;
  }

  @media screen and (min-width: 1280px) {
    margin-right: 20px;
    width: 80px;
  }
}

.logo {
  @media screen and (min-width: 768px) {
    padding: 32px 10px 0 11px;
  }
}

.icon-logo {
  @media screen and (max-width: 767px) {
    fill: $headerTextColor;
  }
}

.social-networks--mobile {
  @media screen and (max-width: 767px) {
    display: block;
  }

  @media screen and (min-width: 768px) {
    display: none;
  }
}

.social-networks--block {
  @media screen and (max-width: 767px) {
    display: none;
  }

  @media screen and (min-width: 768px) {
    display: block;
    transform: rotate(180deg) translateY(-580px);
    transform-origin: top left;
    writing-mode: vertical-rl;
  }
}

.social-networks--list {
  @media screen and (min-width: 768px) {
    display: flex;
  }
}

.social-networks--item {
  @media screen and (max-width: 767px) {
    &:not(:last-child) {
      margin-bottom: 24px;
    }
  }

  @media screen and (min-width: 768px) {
    padding-left: 38px;
    padding-right: 37px;

    &:not(:last-child) {
      margin-bottom: 40px;
    }
  }

  @media screen and (min-width: 1280px) {
    padding-right: 27px;
  }
}

.social-networks--link {
  position: relative;
  font-family: $OpenSans;
  font-style: normal;
  font-weight: 600;
  letter-spacing: 0.2em;

  transition-property: color;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);

  cursor: pointer;

  color: $titleTextColor;

  &:hover,
  &:focus {
    color: $hoverColor;
  }

  @media screen and (max-width: 767px) {
    margin-left: 100px;
    font-size: 14px;
    line-height: 1.36;
  }

  @media screen and (min-width: 768px) {
    font-size: 11px;
    line-height: 1.36;
  }
}

.social-networks--link {
  &.line {
    &:hover,
    &:focus {
      &::before {
        background-color: #ff6c00;
      }
    }

    &::before {
      position: absolute;
      content: '';
      display: inline-block;
      width: 60px;
      height: 1px;
      background-color: $titleTextColor;
    }

    @media screen and (max-width: 767px) {
      &::before {
        bottom: 8px;
        left: -100px;
      }
    }

    @media screen and (min-width: 768px) {
      &::before {
        bottom: 145px;
        left: -20px;
        transform: rotate(90deg);
      }
    }
  }
}

