.site-nav--item {
  &:hover {
    -webkit-animation: 0.1s tremor ease-out infinite;
    animation: 0.1s tremor ease-out infinite;
  }
  @-webkit-keyframes tremor {
    0%,
    25% {
      left: -1px;
      top: -1px;
      -webkit-transform: translateX(-4%);
      transform: translateX(-4%);
    }
    50%,
    100% {
      left: 1px;
      top: 1px;
      -webkit-transform: translateX(4%);
      transform: translateX(4%);
    }
  }
  @-moz-keyframes tremor {
    0%,
    25% {
      left: -1px;
      -webkit-transform: translateX(-4%);
      transform: translateX(-4%);
    }
    50%,
    100% {
      left: 1px;
      -webkit-transform: translateX(4%);
      transform: translateX(4%);
    }
  }
  @-ms-keyframes tremor {
    0%,
    25% {
      left: -1px;
      -webkit-transform: translateX(-4%);
      transform: translateX(-4%);
    }
    50%,
    100% {
      left: 1px;
      -webkit-transform: translateX(4%);
      transform: translateX(4%);
    }
  }
  @-o-keyframes tremor {
    0%,
    25% {
      left: -1px;
      -webkit-transform: translateX(-4%);
      transform: translateX(-4%);
    }
    50%,
    100% {
      left: 1px;
      -webkit-transform: translateX(4%);
      transform: translateX(4%);
    }
  }
  @keyframes tremor {
    0%,
    25% {
      left: -1px;
      -webkit-transform: translateX(-4%);
      transform: translateX(-4%);
    }
    50%,
    100% {
      left: 1px;
      -webkit-transform: translateX(4%);
      transform: translateX(4%);
    }
  }
  @media screen and (max-width: 767px) {
    &:not(:last-child) {
      margin-bottom: 24px;
    }

    &:last-child {
      margin-bottom: 64px;
    }
  }
}

